const accentsMap = new Map([
  ["A", "||||"],
  ["a", "||||"],
  ["E", "|||"],
  ["e", "|||"],
  ["I", "|||"],
  ["i", "|||"],
  ["O", "||||"],
  ["o", "||||"],
  ["U", "|||"],
  ["u", "|||"],
  ["C", ""],
  ["c", ""],
  ["N", ""],
  ["n", ""],
]);

const reducer = (acc, [key]) =>
  acc.replace(new RegExp(accentsMap.get(key), "g"), key);

export const removeDiacritics = (text) => [...accentsMap].reduce(reducer, text);
